import React from 'react';
import {Switch, Route} from 'react-router-dom';
import './App.css';

import HomePage from './pages/homepage/homepage.component';
import ShopPage from './pages/shop/shop.component';

//place the <Header/> outside the routes and switch like so
//now the header will always dispay at the top of ever page 
//in our app
import Header from './components/header/header.component';



function App() {
  return (
    <div>
    <Header/> 
      <Switch>
        <Route   exact path='/ecom-react-app' component={HomePage}/>
        <Route   exact path='/ecom-react-app/shop' component={ShopPage}/>
      </Switch> 
    </div>
  );
}

export default App;
